.card{
    width: 150px;
}
.card .imgbox{
    width: 100%;
    height: 225px;
    position: relative;
}
.card .imgbox img{
    width: 100%;
    height: 100%;
    border-radius: 8px;
}
.card .info>h4{
    width: 150px;
    font-size: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-top: 30px;
}
.card .info>p{
    font-size: 16px;
    color: #cdcaca;
}
.card .circle{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background: #081d1b;
    position: absolute;
    left: 10px;
    transform: translate(-50%,-50%);
    color: #fff;
    text-align: center;
    line-height: 40px;
    font-weight: bold;
    font-size: 12px;
}