.login{
    width: 100%;
}
.login .inner{
    width: 1334px;
    margin: 0 auto;
    padding: 35px 40px 195px;
}
.login .inner .text_wrap{
    margin-bottom: 40px;
}
.login .inner .text_wrap h2{
    margin-bottom: 15px;
}
.login .inner .text_wrap p{
    font-size: 15px;
    letter-spacing: -0.1px;
}
.login .inner span a{
    color: #04b5e4;
}
.login .inner span a:hover{
    text-decoration: underline;
}
.login .inner .text_wrap .first{
    margin-bottom: 20px;
}
.login .inner label{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}
.login .inner label>span{
    margin-bottom: 4px;
}
.login .inner label input{
    width: 100%;
    height: 40px;
    border: 1px solid #dcdddf;
    border-radius: 3px;
    text-indent: 12px;
}
.login .inner label input:focus{
    outline: 1px solid #04b5e4;
    border: none;   
}
.login .inner button{
    width: 90px;
    height: 40px;
    background: #04b5e4;
    color: #fff;
    font-size: 16px;
    border-radius: 8px;
    margin-right: 15px;
}