.modalpopup{
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 20;
}
.modalpopup .modal{
    width: 1334px;
    height: 510px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    overflow: hidden;
    background: #022641;
    position: relative;
}
.modalpopup .modal>img{
    width: 150%;
    position: absolute;
    left: 0;
    top: -150px;
    opacity: 0.5;
}
.modalpopup .modal .btn_close{
    width: 36px;
    height: 36px;
    font-size: 24px;
    color: #fff;
    position: absolute;
    right: 20px;
    top: 20px;
    text-align: center;
    border: 2px solid #fff;
    cursor: pointer;
    z-index: 30;
}
.modalpopup .modal .content{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 25;
    /* border: 1px solid red; */
    padding: 40px 80px;
    display: flex;
    gap: 80px;
    justify-content: center;
}
.modalpopup .modal .content .poster{
    width: 300px;
    border-radius: 16px;
}
.modalpopup .modal .content .all_wrap .text_wrap{
    color: #fff;
}
.modalpopup .modal .content .all_wrap .text_wrap h2{
    margin-bottom: 25px;
}
.modalpopup .modal .content .all_wrap .text_wrap h2 span{
    font-size: 18px;
    font-weight: normal;
    margin-left: 10px;
}
.modalpopup .modal .content .all_wrap .text_wrap .info{
    margin-bottom: 25px;
}
.modalpopup .modal .content .all_wrap .text_wrap h4{
    margin-bottom: 15px;
}
.modalpopup .modal .content .all_wrap .text_wrap .overview{
    line-height: 28px;
}
.modalpopup .modal .content .all_wrap .btn_wrap{
    margin-top: 25px;
}
.modalpopup .modal .content .all_wrap .btn_wrap>button{
    margin-right: 20px;
}
.modalpopup .modal .content .all_wrap .btn_wrap>.circle{
    width: 45px;
    height: 45px;
    border-radius: 100%;
    background: #022641;
}
.modalpopup .modal .content .all_wrap .btn_wrap>button>span{
    color: #fff;
    font-size: 18px;
}
.modalpopup .modal .content .all_wrap .btn_wrap>.play{
    width: 150px;
    height: 20px;
    color: #fff;
    font-size: 16px;
}
