.banner{
    width: 1334px;
    height: 370px;
    overflow: hidden;
}
.banner .all_wrap{
    width: 100%;
    height: 100%;
    position: relative;
}
.banner .all_wrap img{
    width: 100%;
    object-fit: cover;
    position: absolute;
    left: 0;
    top: -20%;
}
.banner .all_wrap .wrap{
    width: 100%;
    padding: 0 45px;
    color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
.banner .all_wrap .wrap h2{
    font-size: 48px;
}
.banner .all_wrap .wrap h3{
    font-size: 32px;
}
.banner .all_wrap .wrap .searchbar{
    width: 100%;
    height: 50px;
    margin-top: 50px;
    position: relative;
}
.banner .all_wrap .wrap .searchbar input{
    width: 100%;
    height: 50px;
    border-radius: 50px;
    border: none;
    text-indent: 25px;
    font-size: 20px;
}
.banner .all_wrap .wrap .searchbar input::placeholder{
    color: #cdcaca;
}
.banner .all_wrap .wrap .searchbar input:focus{
    outline: none;
    border: none;
}
.banner .all_wrap .wrap .searchbar button{
    width: 100px;
    height: 50px;
    border-radius: 50px;
    color: #fff;
    font-weight: bold;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background: linear-gradient(90deg, #1ed5a9 0%, #01b4e4 100%);
}
.banner .all_wrap .wrap .searchbar button:hover{
    color: #000;
}

