.tab{
    width: 100%;
    padding: 30px 0 40px 30px;
    background: url('https://www.themoviedb.org/assets/2/v4/misc/trending-bg-39afc2a5f77e31d469b25c187814c0a2efef225494c038098d62317d923f8415.svg') no-repeat center bottom;
}
.tab .title{
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
}
.tab .title>h4{
    font-size: 26px;
}
.tab .title .btn_wrap{
    display: table;
    height: 30px;
    border: 2px solid #022641;
    border-radius: 30px;
    box-sizing: border-box;
}
.tab .title .btn_wrap button{
    display: table-row;
    height: 30px;
    padding: 0 20px;
    border-radius: 30px;
    font-weight: bold;
    font-size: 16px;
    background: #022641;
    color: #25827b;
    line-height: 30px;
}
.tab .title .btn_wrap button.on{
    background: #fff;
    color: #022641;
}
.tab .swiper{
    width: 100%;
    overflow: hidden;
}
.swiper-wrapper{
    display: flex;
    gap: 20px;
}