.footer{
    width: 100%;
    background: #05253e;
}
.footer .inner{
    width: 1334px;
    padding: 45px 0;
    margin: 0 auto;
    padding: 40px 275px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer .inner .left_wrap .logo{
    width: 130px;
    height: 94px;
    margin-left: 30px;
}
.footer .inner .left_wrap .logo img{
    width: 100%;
    height: 100%;
}
.footer .inner .left_wrap button{
    width: 160px;
    height: 50px;
    border-radius: 8px;
    background: #fff;
    color: #04b5e4;
    margin-top: 45px;
    font-weight: bold;
    font-size: 16px;
}
.footer .inner .right_wrap{
    display: flex;
    align-items: flex-start;
    gap: 40px;
}
.footer .inner .right_wrap ul li{
    color: #fff;
    font-size: 17px;
}
.footer .inner .right_wrap ul li span{
    font-weight: bold;
}