.header{
    width: 100%;
    height: 64px;
    background: #05253e;
    color: #fff;
    font-weight: bold;
}
.header .inner{
    width: 1334px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
}
.header .inner .left_wrap{
    height: 100%;
    display: flex;
    gap: 30px;
    align-items: center;
}
.header .inner .logo{
    width: 154px;
    height: 20px;
    cursor: pointer;
}
.header .inner .logo img{
    width: 100%;
    height: 100%;
}
.header .inner .gnb{
    display: flex;
    gap: 30px;
}
.header .gnb>li{
    height: 30px;
    cursor: pointer;
    position: relative;
    line-height: 30px;
}
.header .inner .right_wrap{
    height: 100%;
    display: flex;
    gap: 30px;
    align-items: center;
}
.header .inner .right_wrap .plus{
    width: 20px;
    height: 20px;
}
.header .inner .right_wrap .plus img{
    width: 100%;
    height: 100%;
}
.header .inner .right_wrap .lang{
    width: 30px;
    height: 30px;
    border: 1px solid #fff;
    border-radius: 3px;
    text-align: center;
    line-height: 30px;
    transition: all 0.2s ease-in-out;
}
.header .inner .right_wrap .lang:hover{
    background: #fff;
    color: #05253e;
}
.header .right_wrap div{
    cursor: pointer;
}
.header .inner .right_wrap span{
    font-size: 24px;
    color: #04b5e4;
}
.header .gnb>li>.depth2{
    position: absolute;
    left: 0;
    top: 30px;
    z-index: 20;
    display: none;
}
.header .gnb>li:hover>.depth2{
    display: block;
}